import React, { lazy, Suspense, useEffect, useState } from 'react';

import Loading from "../Loading/Loading";
import LoadingImg from '../../assets/images/loading2.gif';
import $ from 'jquery';

import { useHistory } from 'react-router-dom';
import axios from '../../axios';

const StudentEntryUI = lazy( () => import('./StudentEntryUI') );

const StudentEntry = () => {

    const history = useHistory();

    const [ ShowLoading, setShowLoading ] = useState( false );
    const [ Step1, setStep1 ] = useState( false );
    const [ Step2, setStep2 ] = useState( false );
    const [ Step3, setStep3 ] = useState( false );
    const [ Step4, setStep4 ] = useState( false );

    const [ StudentInfo, setStudentInfo ] = useState( 
        {
            studentName: '',
            FatherName: '',
            CNIC: '',
            MotherTongue: '',
            Occupation: '',
            MonthlyIncome: '',
            Surname: '',
            Religion: '',
            Gender: '',
            DateOfBirth: '',
            PlaceOfBirth: '',
            PreviousSchool: '',
            OfficeTelephone: '',
            ResidenceTelephone: '',
            Province: '',
            ResidentialAddress: ''
        }
     );

     const [ Registration, setRegistration ] = useState();

    useEffect(
        () => {

            let url = window.location.href;
            let registration = url.split('/').pop().split('=').pop();

            if ( registration !== 'new' )
            {
                
                axios.post('/students/student/', { registration: registration }).then(
                    ( res ) => {
        
                        const moment = require('moment');
                        localStorage.setItem('SaveStepsData', JSON.stringify( res.data ));
                        var d = moment( res.data[0].recording_date ).format('YYYY-MM-DD');
                        let val = {
                            studentName: res.data[0].student_name,
                            FatherName: res.data[0].father_name,
                            CNIC: res.data[0].father_cnic,
                            MotherTongue: res.data[0].mother_tongue,
                            Occupation: res.data[0].father_occupation,
                            MonthlyIncome: res.data[0].monthly_income,
                            Surname: res.data[0].student_surname,
                            Religion: res.data[0].religion,
                            Gender: res.data[0].gender,
                            DateOfBirth: d,
                            PlaceOfBirth: res.data[0].place_of_birth,
                            PreviousSchool: res.data[0].previous_school,
                            OfficeTelephone: res.data[0].office_telephone,
                            ResidenceTelephone: res.data[0].residence_telephone,
                            Province: res.data[0].province,
                            ResidentialAddress: res.data[0].residential_address
                        }
                        
                        setStudentInfo(
                            val
                        );

                        setRegistration( registration );
        
                    }
                ).catch(
                    ( err ) => {
        
                        console.log( err );
        
                    }
                )
                
            }

            setTimeout(() => {
                $('.StepForms').first().addClass('ShowStepForms');
            }, 2000);

            if ( localStorage.getItem('SaveStepsData') )
            {
                let data = JSON.parse( localStorage.getItem('SaveStepsData') );
                setStudentInfo( data[0] );
            }else
            {
                localStorage.setItem('SaveStepsData', JSON.stringify( [ StudentInfo ] ));
            }

            
        }, []
    )
    
    const onChangeHandler = ( e ) => {

        const { name, value } = e.target;
        const val = {
            ...StudentInfo,
            [name]: value
        }

        setStudentInfo( val );
        
    }

    const onStepOne = ( e ) => {

        e.preventDefault();

        localStorage.setItem('SaveStepsData', JSON.stringify( [ StudentInfo ] ));
        $('.StepForms').removeClass('ShowStepForms');

        setShowLoading( true );
        
        setTimeout(() => {
            
            setShowLoading( false );
            $('.StepForms').eq(1).addClass('ShowStepForms');
            setStep1( true );
        }, 1500);


    }

    const onStepTwo = ( e ) => {

        e.preventDefault();

        localStorage.setItem('SaveStepsData', JSON.stringify( [ StudentInfo ] ));
        $('.StepForms').removeClass('ShowStepForms');

        setShowLoading( true );
        
        setTimeout(() => {
            
            setShowLoading( false );
            $('.StepForms').eq(2).addClass('ShowStepForms');
            setStep2( true );
        }, 1500);


    }

    const onStepThird = ( e ) => {

        e.preventDefault();

        localStorage.setItem('SaveStepsData', JSON.stringify( [ StudentInfo ] ));
        $('.StepForms').removeClass('ShowStepForms');

        setShowLoading( true );
        
        setTimeout(() => {
            
            setShowLoading( false );
            $('.StepForms').eq(3).addClass('ShowStepForms');
            setStep3( true );
        }, 1500);


    }

    const onStepForth = ( e ) => {

        e.preventDefault();

        const d = new Date();
        let val = {
            ...StudentInfo,
            date: d,
            time: d.toTimeString(),
            registration: Registration ? Registration : null
        }

        if ( !Registration )
        {
            
            axios.post('/studententry', val).then(
                () => {
    
                    setStudentInfo(
                        {
                            studentName: '',
                            FatherName: '',
                            CNIC: '',
                            MotherTongue: '',
                            Occupation: '',
                            MonthlyIncome: '',
                            Surname: '',
                            Religion: '',
                            Gender: '',
                            DateOfBirth: '',
                            PlaceOfBirth: '',
                            PreviousSchool: '',
                            OfficeTelephone: '',
                            ResidenceTelephone: '',
                            Province: '',
                            ResidentialAddress: ''
                        }
                    )
            
                    localStorage.setItem('SaveStepsData', JSON.stringify( [ {} ] ));
                    $('.StepForms').removeClass('ShowStepForms');
            
                    setShowLoading( true );
                    
                    setTimeout(() => {
                        
                        setShowLoading( false );
                        $('.StepForms').eq(4).addClass('ShowStepForms');
                        setStep4( true );
            
                        setTimeout(() => {
                            let i = 0;
                            let txt = "Student Information has been entered successfully.";
                            let speed = 50;
                
                            function typeWriter() {
                              if (i < txt.length) {
                                document.getElementById("StudentInfoMsg").innerHTML += txt.charAt(i);
                                i++;
                                setTimeout(typeWriter, speed);
                              }
            
                              if ( i === txt.length )
                              {
                                $('.StepForms .container-fluid').removeClass('d-none');
                              }
                            }
                
                            typeWriter();
                        }, 500);
            
                    }, 1500); 
    
                }
            ).catch(
                ( err ) => {
    
                    console.log( err );
    
                }
            )

        }else
        {

            axios.post('/students/student/edit', val).then(
                () => {
    
                    setStudentInfo(
                        {
                            studentName: '',
                            FatherName: '',
                            CNIC: '',
                            MotherTongue: '',
                            Occupation: '',
                            MonthlyIncome: '',
                            Surname: '',
                            Religion: '',
                            Gender: '',
                            DateOfBirth: '',
                            PlaceOfBirth: '',
                            PreviousSchool: '',
                            OfficeTelephone: '',
                            ResidenceTelephone: '',
                            Province: '',
                            ResidentialAddress: ''
                        }
                    )
            
                    localStorage.setItem('SaveStepsData', JSON.stringify( [ {} ] ));
                    $('.StepForms').removeClass('ShowStepForms');
            
                    setShowLoading( true );
                    
                    setTimeout(() => {
                        
                        setShowLoading( false );
                        $('.StepForms').eq(4).addClass('ShowStepForms');
                        setStep4( true );
            
                        setTimeout(() => {
                            let i = 0;
                            let txt = "Student Information has been updated successfully.";
                            let speed = 50;
                
                            function typeWriter() {
                              if (i < txt.length) {
                                document.getElementById("StudentInfoMsg").innerHTML += txt.charAt(i);
                                i++;
                                setTimeout(typeWriter, speed);
                              }
            
                              if ( i === txt.length )
                              {
                                $('.StepForms .container-fluid').removeClass('d-none');
                              }
                            }
                
                            typeWriter();
                        }, 500);
            
                    }, 1500); 
    
                }
            ).catch(
                ( err ) => {
    
                    console.log( err );
    
                }
            )

        }

    }

    const BackToList = () => {

        history.push('/students/list');

    }

    const PreviousStep = ( step ) => {

        if ( step === 'one' ) 
        {
            $('.StepForms').removeClass('ShowStepForms');
            $('.StepForms').first().addClass('ShowStepForms');
            setStep1( false );
        }

        if ( step === 'two' ) 
        {
            $('.StepForms').removeClass('ShowStepForms');
            $('.StepForms').eq(1).addClass('ShowStepForms');
            setStep2( false );
        }

        if ( step === 'three' ) 
        {
            $('.StepForms').removeClass('ShowStepForms');
            $('.StepForms').eq(2).addClass('ShowStepForms');
            setStep3( false );
        }

    }

    const NewEntry = () => {

        setStep1( false );
        setStep2( false );
        setStep3( false );
        setStep4( false );
        $('.StepForms .container-fluid').addClass('d-none');
        document.getElementById("StudentInfoMsg").innerHTML = '';
        $('.StepForms').removeClass('ShowStepForms');
        $('.StepForms').first().addClass('ShowStepForms');

    }

    return (
        <Suspense fallback={ <Loading image={ LoadingImg } txt='Loading....' width="50" height="50" unit="vh" /> }>
            <StudentEntryUI
                Step1={ Step1 }
                Step2={ Step2 }
                Step3={ Step3 }
                Step4={ Step4 }
                ShowLoading={ ShowLoading }

                StudentInfo={ StudentInfo }
                onChangeHandler={ onChangeHandler }
                BackToList={ BackToList }
                NewEntry={ NewEntry }
                PreviousStep={ PreviousStep }

                onStepOne={ onStepOne }
                onStepTwo={ onStepTwo }
                onStepThird={ onStepThird }
                onStepForth={ onStepForth }
            />
        </Suspense>
    )

}

export default StudentEntry;